import React from 'react';
import PropTypes from 'prop-types';
import { Card, CardImg, CardTitle, Button } from 'reactstrap';

import { MovieCardBody } from './style';

const propTypes = {};

const defaultProps = {};

export default function MovieCard({ title, poster }) {
  return (
    <MovieCardBody>
      <CardImg top width="100%" src={poster} alt={title} />
      <CardTitle>{title}</CardTitle>
      <Button>Watch</Button>
    </MovieCardBody>
  );
}

MovieCard.propTypes = propTypes;
MovieCard.defaultProps = defaultProps;